@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;500;700&display=swap);
*{margin:0;padding:0;font-family:'Noto Sans KR', sans-serif}li{list-style:none}a{text-decoration:none;color:#000}

nav{display:flex;justify-content:space-between;align-items:center;height:3rem}nav>h1{display:flex;height:2.5rem}nav>h1>a{display:flex;align-items:center}nav>h1>a>img{margin:0 1rem 0 2rem;width:2rem;height:2rem}nav>ul{display:flex;align-items:center;height:100%}nav>ul>li{width:6rem;text-align:center}

header{position:relative;overflow-x:hidden}header .slide-wrap{display:flex;transition:-webkit-transform 0.35s ease-in-out;transition:transform 0.35s ease-in-out;transition:transform 0.35s ease-in-out, -webkit-transform 0.35s ease-in-out;height:40vh}header .slide-wrap>li{position:relative}header .slide-wrap>li>img{object-fit:cover;object-position:center center;width:100vw;height:40vh}header .slide-wrap>li .list-info{position:absolute;top:50%;left:5rem;-webkit-transform:translateY(-50%);transform:translateY(-50%)}header .slide-wrap>li .list-info>h2{text-shadow:2px 2px 5px #fff}header .slide-wrap>li .list-info>p{margin-bottom:1rem;text-shadow:1px 1px 3px #fff}header .slide-wrap>li .list-info>a{padding:0.5rem 1.5rem;background-color:rgba(255,255,255,0.5)}header .side-arrow-wrap{display:flex;position:absolute;justify-content:space-between;top:50%;width:100%;color:#fff;font-size:4rem;-webkit-transform:translateY(-50%);transform:translateY(-50%)}header .side-arrow-wrap>svg{cursor:pointer;display:none;background-color:rgba(0,0,0,0.4)}header:hover>div>svg{display:block}header .slide-length{position:absolute;bottom:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}header .slide-length>button{cursor:pointer;margin:1rem;width:20px;height:20px;border-radius:50%;border:0;color:transparent;background-color:#aaa}header .slide-length>.slideBtn{background-color:#000}

.container1{padding:5vh 10vw}.container1>h2{font-size:2rem}.container1 .hot-issue-wrap{display:flex;justify-content:space-between}.container1 .hot-issue-wrap>li{width:23%}.container1 .hot-issue-wrap>li>a>img{width:100%}.container1 .hot-issue-wrap>li>.image-content{position:relative}.container1 .hot-issue-wrap>li>.image-content>h3{line-height:3rem}.container1 .hot-issue-wrap>li>.image-content>.like-count{position:absolute;display:flex;align-items:center;top:0.7rem;right:0}.container1 .hot-issue-wrap>li>.image-content>.like-count>svg{color:red}.container1 .hot-issue-wrap>li>.image-content>.hot-issue-content{overflow:hidden;text-overflow:ellipsis;white-space:nowrap}@media (min-width: 758px) and (max-width: 1023px){.container1{padding:5vh 5vw}.container1 .hot-issue-wrap{flex-wrap:wrap}.container1 .hot-issue-wrap>li{margin-bottom:5vh;width:49%}}@media (min-width: 335px) and (max-width: 757px){.container1{padding:3vh 3vw}.container1 .hot-issue-wrap{flex-direction:column;flex-wrap:wrap}.container1 .hot-issue-wrap>li{margin-bottom:5vh;width:100%}}

.container2{padding:5vh 10vw;background:#f1f2f3}.container2>h2{font-size:2rem}.container2>table{border-spacing:0;width:100%}.container2>table>thead>tr>th{line-height:3rem;border-bottom:1px solid #aaa;font-size:1.2rem}.container2>table>thead>tr>th:first-child{width:5%}.container2>table>thead>tr>th:nth-child(2){width:65%}.container2>table>thead>tr>th:nth-child(3){width:10%}.container2>table>thead>tr>th:nth-child(4){width:15%}.container2>table>thead>tr>th:last-child{width:5%}.container2>table>tbody>tr>td{line-height:2rem;text-align:center;border-top:1px solid #aaa}@media (min-width: 758px) and (max-width: 1023px){.container2{padding:5vh 5vw}}@media (min-width: 335px) and (max-width: 757px){.container2{padding:3vh 3vw}.container2 th:first-child{width:15%}.container2 th:nth-child(2){width:85%}.container2 th:nth-child(3){display:none}.container2 th:nth-child(4){display:none}.container2 th:last-child{display:none}.container2 td:first-child{width:15%}.container2 td:nth-child(2){width:85%}.container2 td:nth-child(3){display:none}.container2 td:nth-child(4){display:none}.container2 td:last-child{display:none}}

footer{padding:5vh 0;text-align:center}footer>p{display:flex;justify-content:center;align-items:center}footer>p>img{width:2rem;height:2rem}footer>p>a{height:100%;margin:1%}footer>p>a>svg{width:2rem;height:2rem}

